<template>
    <div class="join">
        <div class="join-title">
            <p class="sub-title">歡迎登入</p>
            <p class="title">WELLCOME</p>
        </div>
        <div class="join-content">
            <p class="title">一般用戶註冊</p>
            <p class="sub-title">MEMBER REGISTER</p>

            <el-steps class="steps" :active="active" align-center finish-status="success">
                <el-step title="註冊方式" icon="el-icon-user-solid"></el-step>
                <el-step title="註冊帳號" icon="el-icon-s-order"></el-step>
                <el-step title="手機驗證" icon="el-icon-mobile-phone"></el-step>
                <el-step title="註冊成功" icon="el-icon-check"></el-step>
            </el-steps>
            <div class="join-info">
                <div v-if=" active === 0 ">
                    <p class="join-info-title">STEP 1 - 同意會員條款</p>
                    <div class="join-info-terms">
                        會員條款<br><br>
                        本服務條款之訂立係基於XX科技股份有限公司( 以下稱「本公司」) 旗下所屬之汽車交易網以下簡稱爲，在其所提供的網上服務時，會員必須是具備完全行為能力的自然人、或合法登記的法人或團體，並加入會員後即可開始， 所提供的服務。本服務條款之訂立係釐清並規範本公司和會員之間的權利、義務和責任關係爲目的。
                    </div>
                    <p class="check-txt">註冊前請先仔細閱讀並  <el-checkbox v-model="checked">同意「會員條款」等所載內容</el-checkbox></p>
                    <br><br><br><br>
                    <p class="join-info-title">STEP 2 - 選擇註冊方式</p>
                    <div class="join-btn user" @click="next()"> <img src="~@/assets/img/login/user.png" alt="" srcset="">註冊一個新帳號</div>
                    <div class="join-btn fb" ><img src="~@/assets/img/login/fb.png" alt="" srcset="">使用 Facebook 帳號註冊</div>
                    <div class="join-btn google" ><img src="~@/assets/img/login/google.png" alt="" srcset="">使用 Google 帳號註冊</div>
                </div>
                <div v-if=" active === 1 " class="join-info-1">
                    <ul>
                        <li>
                            <span>姓名</span>
                            <input class="join-input" type="text" placeholder="請填寫" >
                        </li>
                        <li>
                            <span>E-mail</span>
                            <input class="join-input" type="text" placeholder="請填寫" >
                        </li>
                        <li>
                            <span>密碼
                                <el-tooltip class="item" effect="dark" content="請輸入英文數字至少8字。" placement="top">
                                <i class="el-icon-info" />
                                </el-tooltip>
                            </span>
                            <input class="join-input" type="text" placeholder="請填寫" >
                        </li>
                        <li>
                            <span>確認密碼
                                <el-tooltip class="item" effect="dark" content="請輸入英文數字至少8字。" placement="top">
                                <i class="el-icon-info" />
                                </el-tooltip>
                            </span>
                            <input class="join-input" type="text" placeholder="請填寫">
                        </li>
                        <li >
                            <span>地址</span>
                            <input class="join-input" type="text" placeholder="縣市" >
                        </li>
                        <li>
                            <span style="opacity: 0;">地址</span>
                            <input class="join-input" type="text" placeholder="鄉鎮區" >
                        </li>
                        <li class="address">
                            <span style="opacity: 0;">地址</span>
                            <input class="join-input" type="text" placeholder="請輸入地址" >
                        </li>

                    </ul>
                    <div class="join-btn " @click="next()">確認送出</div>
                </div>
                <div v-if=" active === 2 ">
                    <p class="join-info-txt">請先輸入手機號碼並取得簡訊驗證碼，<br>再將輸入驗證碼後送出驗證。</p>
                    <div class="join-input-phone">
                        <input class="join-input" type="text" placeholder="手機號碼" v-model="phone">
                        <span>發送驗證</span>
                    </div>
                    <input class="join-input" type="text" placeholder="輸入簡訊驗證碼" v-model="phoneSms">
                    <div class="join-btn" @click="next()">送出確認</div>
                </div>
                <div v-if=" active === 3 " class="join-info-3">
                    <i class="el-icon-check icon" />
                    <p class="check-txt">會員註冊成功</p>
                    <p class="join-info-txt">恭喜您註冊成功！即可登入網站</p>
                    <div class="router">
                        <router-link to="/login">我的會員</router-link> /
                        <router-link to="/">回到首頁</router-link> 
                    </div>

                </div>

            </div>
        </div>


    </div>
</template>

<script>
export default {
    name:'join',
    data() {
        return{
            active: 0,
            state: '',
            phone: '',
            phoneSms: '',
            pws: '',
            checked:false,
        }
    },
    methods: {
        next() {
        this.checked? this.active++ : this.active
        },
        openMail() {
            this.next() 
            this.state = 'mail'
        },
        openPhone() {
            this.next()
            this.state = 'phone'
        }
    }

}
</script>

<style lang="scss">
.join {
    padding: 105px 0;
    .join-title {
        background: url('~@/assets/img/login/login-banner.png') no-repeat center;
        background-size: cover;
    }  
    .join-content {
        text-align: center;
        margin: 100px 0;
        .title {
            font-size: 40px;
            color: #333333;
            font-weight: bold;
            margin-bottom: 10px;
        }
        .sub-title {
            font-size: 25px;
            color: #ef6d38;
            margin-bottom: 100px;
        }
        .steps {
            max-width: 650px;
            margin: auto;
            margin-bottom: 50px;
        }
        .join-info {
            background: #f4f4f4;
            padding: 50px 0;
            .join-btn {
                max-width: 400px;
                width: 85%;
                line-height: 70px;
                color: #fff;
                border-radius: 20px;
                background: #555;
                margin: 20px auto;
                cursor: pointer;
                transition: all .3s;
                &:hover {
                    filter: brightness(130%)
                }
            }
            .fb {
                background: #436092;
            }
            .google {
                background: #df443c;
            }
            .fb,.google,.user {
                position: relative;
                overflow: hidden;
                img {
                    background: #00000025;
                    padding: 18px;
                    position: absolute;
                    left: 0;
                }
            }
            .phone {
                background: #555555;
            }
            
            .join-info-title {
                color: #ef6d38;
                font-size: 24px;
                margin-bottom: 50px;
            }
            .join-info-txt{
                margin-bottom: 50px;
                line-height: 25px;
                color: #555555;
            }
            .join-input {
                max-width: 400px;
                width: 85%;
                line-height: 60px;
                border-radius: 20px;
                border: 1px #555 solid;
                padding: 0px 20px;
                box-sizing: border-box;
                margin-bottom: 20px;
            
            }
            .join-input-phone {
                position: relative;
                max-width: 400px;
                width: 85%;
                margin: auto;
                .join-input { 
                    width: 100%;
                }
                span {
                    right: 3px;
                    top: 3px;
                    position: absolute;
                    background: #ef6d38;
                    color: #fff;
                    border-radius: 0px 18px 18px 0px;
                    line-height: 56px;
                    padding: 0 10px; 
                    cursor: pointer;
                    &::after {
                        content: " ";
                        position: absolute;
                        left: -30px;
                        border-style: solid;
                        border-width: 0 0 56px 30px;
                        border-color: transparent transparent #ef6d38 transparent;
                    }  
                }
            }
            .join-info-terms {
                max-width: 850px;
                width: 80%;
                margin: auto;
                background: #fff;
                border-radius: 20px;
                border: 1px #dddddd solid;
                color: #999999;
                text-align: left;
                padding: 20px;
                line-height: 28px;
            }
            .check-txt {
                font-size: 14px;
                color: #606266;
                margin: 10px 0;
            }
            .join-info-1 {
                ul {
                    max-width: 800px;
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: center;
                    margin: auto;
                    li {
                        max-width: 400px;
                        width: 85%;
                        span {
                            color: #ef6d38;
                            display: block;
                            text-align: left;
                            padding-left: 50px;
                            margin-bottom: 5px;                 
                        }
                    }
                    .address{
                        max-width: 100%;
                        width: 100%;
                        .join-input { 
                            width: 100%;
                            max-width: 93%;
                            
                        }
                        @media screen and (max-width: 814px){
                            max-width: 400px;
                            .join-input { 
                                width: 100%;
                                max-width: 100%;               
                            }
                        }
                    }
                }
                
                .join-input {
                    border: 1px #dddddd solid;
                }
            }
            .join-info-3 {
                padding: 50px 0;
                .icon {
                    padding: 10px;
                    background: #2bb673;
                    border-radius: 50px;
                    font-size: 60px;
                    color: #fff;
                }
                .check-txt {
                    color: #2bb673;
                    font-size: 24px;
                    margin: 15px 0 30px 0;
                }
                .join-info-txt {
                    margin-bottom: 20px;
                }
                .router {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    a {
                        color: #ef6d38;
                        margin: 0 5px;
                    }
                }
            }
        }
        @media screen and (max-width: 814px){ 
            .join-info .join-info-1 ul li {
                width: 100%;
                span {
                    padding-left: 25px;
                }
            }
            .join-info .join-input {
                width: 100%;
            }
        }
    }
}

</style>


